<template>
	<div class="staffManagement">
		<div class="staffManagement_top">
			<div class="staffManagement_title">
				员工管理
			</div>
		</div>

		<!-- <div class="staffManagement_in">
			<DatePicker type="date" placeholder="Select date" style="width: 200px"></DatePicker>--
			<DatePicker type="date" placeholder="Select date" style="width: 200px"></DatePicker>
			<Button type="info">搜索</Button>
		</div> -->
		<div class="staffManagement_bot">
			<Table :columns="columns1" :data="data1" ref="tableFams" :height="tableHeight">
				<template slot-scope="{ row }" slot="portrait">
					<div style="display: flex;align-items: center;text-align: left;">
						<div class="mgright_15">
							<Avatar :src="row.portrait" />
						</div>
						<div>{{row.name}}</div>
					</div>
				</template>
				<template slot-scope="{ row }" slot="isPush">
					<i-switch v-model="row.isPush" @on-change="handChange($event,row)"></i-switch>
				</template>
			</Table>
		</div>

		<div class="staffManagement_page">
			<Page :total="total" :page-size="limit" show-total show-elevator @on-page-size-change="handleSizeChange"
				@on-change='handleCurrentChange' />
		</div>

	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	export default {
		data() {
			return {
				columns1: [{
						title: '排序',
						type: 'index',
						align: 'center'
					},
					// {
					// 	title: '昵称',
					// 	key: 'name',
					// 	align: 'center'
					// },
					{
						title: '员工姓名',
						slot: 'portrait',
						width: 250,
					},
					{
						title: '手机号',
						key: 'phone',
						align: 'center'
					},
					{
						title: '加入时间',
						key: 'creationTime',
						align: 'center'
					},
					{
						title: '是否推送',
						slot: 'isPush',
						align: 'center'
					},
				],
				data1: [],
				page: 1, //页码
				limit: 15, // 每页的数量,
				total: 0,
				wxAppId: localStorage.getItem('wxAppId'),
				tableHeight: 607,

			}
		},
		mounted() {
			window.addEventListener('resize', this.getTableHeight);
			this.getTableHeight();

			this.handgetStuffList()
		},
		methods: {
			getTableHeight() {
				console.log(this.$refs.tableFams);
				if (!this.$refs.tableFams) {
					return
				}
				this.tableHeight = window.innerHeight - this.$refs.tableFams.$el.offsetTop - 200;
			},
			handgetStuffList() {
				const data = {
					page: this.page,
					limit: this.limit,
					wxAppId: this.wxAppId,
				}
				indexApiModules.getStuffList(data).then(res => {
					this.data1 = res.data.list
					this.data1.forEach(item => {
						item.isPush = Boolean(item.isPush)
					})
					this.total = res.data.pageData.count
					console.log('this.data1', this.data1)
				})
			},
			handChange(status, row) {
				let isPush = Number(status)
				indexApiModules.setStuffIsPush({
					isPush: isPush,
					wxAppId: this.wxAppId,
					companyUserId: row.companyUserId
				}).then(res => {
					console.log('res', res)
					this.$Message.success('修改成功');
				}).catch(e => {
					console.log(e)
					this.handgetStuffList()
				})
			},

			// 当前页
			handleCurrentChange(val) {
				this.page = val
				this.handgetStuffList()
			},
			//每页数
			handleSizeChange(val) {
				this.limit = val;
				this.page = 1;
				this.handgetStuffList()
			},
		},
		created: function() {

		}
	};
</script>
<style lang="less" scoped>
	.staffManagement {
		font-size: 16px;
		color: #333333;

		.staffManagement_top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 14px 16px;
			border-bottom: 1px solid #DDDDDD;

			.staffManagement_title {
				display: flex;
				align-items: center;
				font-size: 16px;
				color: #333333;

				.staffManagement_title_item {
					margin-right: 80px;
					color: #333333;
					font-size: 16px;
				}

				.color-blue {
					color: #2d8cf0;
				}
			}
		}

		.staffManagement_in {
			padding: 14px 16px;
		}

		.staffManagement_page {
			text-align: center;
			margin-top: 20px;
			padding-bottom: 20px;
		}

	}
</style>
